import Accordion from "@material-ui/core/Accordion/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary/AccordionSummary";
import CircularProgress from "@material-ui/core/CircularProgress/CircularProgress";
import React from "react";
import {MyButton, notEmpty, useEndPoint} from "../my_util/Util";
import {AccordionDetails, Typography} from "@material-ui/core";
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {useTranslation} from "react-i18next";
import _, {Dictionary} from "lodash";

interface GAsset {
    browser_download_url: string;
    name: string;//filename
    download_count: number;
}

interface GReleaseInfo {
    assets: GAsset[];
    tag_name: string;
    body: string;
    prerelease: boolean;
    published_at: string;
}

var findModVersion = new RegExp('(?<=immersive\-portals\-).+?(?=\-)');
var findMcVersion = new RegExp('(?<=mc).+?(?=\-)');

function getModVersionFromModJarName(modJarName: string) {
    var found = findModVersion.exec(modJarName);
    if (found == null) {
        return null;
    }
    return found[0];
}

function getMcVersionFromModJarName(modJarName: string) {
    var found = findMcVersion.exec(modJarName);
    if (found == null) {
        return null;
    }
    return found[0];
}

interface ToDisplay {
    mcVersion: string;
    modVersion: string;
    downloadUrl: string;
    upgradeInfo: string;
    isPreRelease: boolean;
    publishTime: string;
    downloadCount: number;
}

function DownloadTerm(props: { data: ToDisplay }) {
    let {t, i18n} = useTranslation();
    
    return <Accordion key={props.data.downloadUrl} style={{backgroundColor: '#af52bf', color: 'white'}}>
        <AccordionSummary
            expandIcon={<ExpandMoreIcon/>}
            id={props.data.downloadUrl}
        >
            <Typography>{
                (props.data.isPreRelease ? '[Pre-release] ' : '') + props.data.modVersion
            }</Typography>
        </AccordionSummary>
        <AccordionDetails style={{display: 'flex', flexDirection: 'column', alignItems: 'flex-start'}}>
            <Typography>{props.data.upgradeInfo}</Typography>
            <div style={{margin: 10}}/>
            <MyButton onClick={() => {
                window.open(props.data.downloadUrl, '_blank')
            }}>{t('Download')}</MyButton>
            <div style={{margin: 10}}/>
            <Typography variant='caption'>
                {'Published at ' + props.data.publishTime}
            </Typography>
            <Typography variant='caption'>
                {'Download count (GitHub): ' + props.data.downloadCount}
            </Typography>
        </AccordionDetails>
    </Accordion>;
}

export function IPDownload(props: {}) {
    let {t, i18n} = useTranslation();
    
    const {data: resultData, complete: resultComplete} = useEndPoint(
        "https://api.github.com/repos/qouteall/immersiveportalsmod/releases"
    );
    
    if (!resultComplete) {
        return <CircularProgress/>
    }
    
    const releaseInfo = resultData as unknown as GReleaseInfo[];
    
    // console.log(releaseInfo)
    
    if (!releaseInfo) {
        return <p>error</p>
    }
    
    const toDisplay: ToDisplay[] = releaseInfo
        .map((releaseInfo: GReleaseInfo) => {
            var modJarAssets = releaseInfo.assets.filter(asset => asset.name.indexOf('fabric') >= 0);
            if (modJarAssets.length === 0) {
                return null;
            }
            var asset = modJarAssets[0];
            
            const publishDate = releaseInfo.published_at.substr(
                0, releaseInfo.published_at.indexOf('T')
            );
            
            return {
                mcVersion: getMcVersionFromModJarName(asset.name),
                modVersion: getModVersionFromModJarName(asset.name),
                downloadUrl: asset.browser_download_url,
                upgradeInfo: releaseInfo.body,
                isPreRelease: releaseInfo.prerelease,
                publishTime: publishDate,
                downloadCount: asset.download_count
            } as ToDisplay;
        })
        .filter(notEmpty);
    
    const groupedByMcVersion: Dictionary<ToDisplay[]> = _.groupBy(
        toDisplay,
        (r: ToDisplay) => r.mcVersion
    );
    
    
    return <div>
        {_.toPairsIn(groupedByMcVersion).map((pair) => {
            const [mcVersion, termList] = pair;
            
            const firstRelease: number = termList.findIndex((term) => !term.isPreRelease);
            if (firstRelease !== -1) {
                //remove the first release and insert into the first
                termList.splice(0, 0, ...(termList.splice(firstRelease, 1)))
            }
            
            return <Accordion key={pair[0]} style={{backgroundColor: '#9500ae', color: 'white'}}>
                <AccordionSummary>
                    {mcVersion}
                </AccordionSummary>
                
                <AccordionDetails>
                    <div style={{width: '100%'}}>
                        {termList.map(r => <DownloadTerm data={r} key={r.downloadUrl}/>)}
                    </div>
                </AccordionDetails>
            </Accordion>;
        })}
        {/*{toDisplay.map(r => <DownloadTerm data={r}/>)}*/}
    </div>
}