import React, {useEffect, useRef} from "react";
import {useHistory, useLocation} from "react-router-dom";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import LinkIcon from "@material-ui/icons/Link";

export function getHeadingFontSize(level: number) {
    let fontSize = 55;
    
    switch (level) {
        case 1:
            fontSize = 36
            break
        case 2:
            fontSize = 32
            break
        case 3:
            fontSize = 27
            break
        case 4:
            fontSize = 22
            break
        case 5:
            fontSize = 20
            break
    }
    return fontSize;
}

export function getHeadingColor(level: number) {
    let background = "#ed4b82";
    switch (level) {
        case 1:
            background = "#4dabf5"
            break
        case 2:
            background = "#00a152"
            break
        case 3:
            background = "#ffac33"
            break
        case 4:
            background = "#ed4b82"
            break
        case 5:
            background = "#4dabf5"
            break
    }
    return background;
}

export function WikiHeading(props: { level: number, children: any, node: any }) {
    const myRef: React.MutableRefObject<any> = useRef()
    const location = useLocation()
    const history = useHistory()
    
    const headingHash = props.node.myHeadingHash;
    
    useEffect(() => {
        if (location.hash) {
            // console.log(headingHash)
            if (location.hash === headingHash) {
                myRef.current.scrollIntoView({behavior: 'smooth'});
            }
        }
    }, [headingHash, location.hash])
    
    const level = props.level;
    const fontSize = getHeadingFontSize(level);
    const background = getHeadingColor(level);
    
    return <div id={headingHash} style={{
        display: "flex", flexDirection: "row", alignItems: "stretch"
    }}>
        <div style={{
            display: "inline-flex", width: 5, background: background,
            marginTop: 4, marginBottom: 4, marginRight: 10,
            flexShrink: 0
        }}/>
        <Typography ref={myRef} style={{fontSize: fontSize}}>
            {props.children}
            <IconButton onClick={() => {
                history.push(headingHash)
            }}>
                <LinkIcon/>
            </IconButton>
        </Typography>
    </div>;
}