import React, {Component, CSSProperties, useState} from "react";
import {createStyles, Grid, Theme} from "@material-ui/core";
import Paper from '@material-ui/core/Paper';
import {getIsVerticalMobile, getPublicResourcePath, MyButton} from "../my_util/Util";
import Button from "@material-ui/core/Button";
import Card from '@material-ui/core/Card';
import CardContent from "@material-ui/core/CardContent";
import CardActionArea from "@material-ui/core/CardActionArea";
import {TestAnimated} from "./TestAnimation";
import {Link, useHistory} from "react-router-dom";
import {MyBanner} from "./MyBanner";

import {useTranslation} from 'react-i18next';
import {TFunction} from "i18next";
import ButtonBase from "@material-ui/core/ButtonBase";

export function IndexPage(props: {}) {
    
    let history = useHistory();
    
    
    let {t, i18n} = useTranslation();
    
    const cardStyle: CSSProperties = getIsVerticalMobile() ? {margin: 20} : {
        margin: 20, marginLeft: 100, marginTop: 50
    };
    
    return <div className="My-Page">
        <MyBanner text='qouteall' color='#ff4569' pageIconPath='/benzene.jpg'/>
        <div className="App-header">
            <div className="Mask"/>
            <div className="Right-section">
                
                <p className="Header-text">
                    qouteall
                </p>
                <p className="Sub-Header">
                    {t('main_desc')}
                </p>
            </div>
            
            <img src={getPublicResourcePath('/headerImage.png')} className="Header-image" alt="what"/>
        
        
        </div>
        
        <div style={cardStyle}>
            <Card style={{maxWidth: '80vh'}} square={false} elevation={10}>
                <CardActionArea style={{
                    display: 'flex', flexDirection: 'row', justifyContent: 'flex-start',
                    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)'
                }} onClick={() => {
                    history.push('/immptl')
                }}>
                    <img src={getPublicResourcePath('/immptl.png')} alt='i am alt' style={{width: 100}}/>
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'flex-start',
                        marginLeft: 20
                    }}>
                        <label style={{
                            textTransform: 'none',
                            color: 'black',
                            fontSize: 'calc(8px + 2vmin)'
                        }}>
                            {t("immptl")}
                        </label>
                        <label style={{
                            textTransform: 'none',
                            color: 'black',
                            fontSize: 'calc(5px + 2vmin)',
                        }}>
                            {t("immptl_desc")}
                        </label>
                    </div>
                </CardActionArea>
            </Card>
        </div>
    </div>;
    
}
