import {MenuItem, Select} from "@material-ui/core";
import React, {useEffect} from "react";
import AppBar from '@material-ui/core/AppBar';
import Typography from "@material-ui/core/Typography";
import Toolbar from "@material-ui/core/Toolbar";
import i18next from "i18next";
import {useHistory, useLocation} from "react-router-dom";

import TranslateIcon from '@material-ui/icons/Translate';
import {getCurrentLanguage, getStringAfter} from "../my_util/Util";
import Button from "@material-ui/core/Button";

export function MyBanner(props: {
    text: string, color: string, children?: any,
    sticky?: boolean, pageIconPath: string
}) {
    const location = useLocation();
    
    let history = useHistory();
    var currLanguage = getCurrentLanguage();
    
    var hyphenIndex = currLanguage.indexOf('-');
    if (hyphenIndex !== -1) {
        currLanguage = currLanguage.substr(0, hyphenIndex);
    }
    
    // console.log(currLanguage)
    
    useEffect(() => {
        document.title = props.text;
        // @ts-ignore
        document.getElementById('page_icon').href = props.pageIconPath;
    }, []);
    
    return <AppBar style={{
        position: props.sticky ? 'sticky' : 'relative', backgroundColor: props.color,
        margin: 0, padding: 0,
        top: props.sticky ? 0 : undefined
    }}>
        <Toolbar>
            {props.children}
            <Button onClick={() => {
                const currPath = location.pathname;
                const i = currPath.lastIndexOf('/');
                if (i !== -1) {
                    history.push(currPath.substr(0, i));
                }
            }} style={{textTransform: 'none'}}>
                <Typography variant='h6' style={{color: 'white'}}>{props.text}</Typography>
            </Button>
            
            {/*<TranslateIcon style={{marginLeft: 'auto', marginRight: '1vw'}}/>*/}
            
            {/*<Select id='language-select' style={{*/}
            {/*    color: 'white'*/}
            {/*}} value={currLanguage} onChange={(a) => {*/}
            {/*    var languageStr = a.target.value;*/}
            {/*    i18next.changeLanguage(languageStr as string)*/}
            {/*}}>*/}
            {/*    <MenuItem value={'zh'}>简体中文</MenuItem>*/}
            {/*    <MenuItem value={'en'}>English</MenuItem>*/}
            {/*</Select>*/}
        </Toolbar>
    
    
    </AppBar>;
}