import React, {CSSProperties} from "react";

import {Transition} from 'react-transition-group';
import {Button} from "@material-ui/core";

const transitionStyles = {
    // entering: {clipPath: 'circle(100%)',},
    // entered: {clipPath: 'circle(100%)',},
    // exiting: {clipPath: 'circle(0%)',},
    // exited: {clipPath: 'circle(0%)',},
    
    entering: {clipPath: 'inset(0%)',},
    entered: {clipPath: '',},
    exiting: {clipPath: 'inset(0% 0% 100% 0%)',},
    exited: {clipPath: 'inset(0% 0% 100% 0%)',},
};


export const TestAnimated = (props: { shown: boolean, children: any,duration:number }) => {
    const defaultStyle = {
        transition: `clip-path ${props.duration}ms ease-in`,
    }
    
    return (
        <Transition in={props.shown} timeout={props.duration}>
            {state => {
                // console.log(state)
                return (
                    <div style={{
                        ...defaultStyle,
                        // @ts-ignore
                        ...transitionStyles[state]
                    }}>
                        {props.children}
                    </div>
                );
            }}
        </Transition>
    );
};