// @ts-ignore

var allTypes = Object.keys(defaultRenderers);

type Content = Array<HeadingWithContent | Array<Content>>

interface HeadingWithContent {
    myType: 'heading_with_content',
    headingElement: any | null,//only the root has null heading
    contents: Array<HeadingWithContent | any>
}

function isHeading(node: any) {
    return node.type === 'heading';
}

function isHeadingWithContent(obj: any): obj is HeadingWithContent {
    return obj.myType === 'heading_with_content';
}

const toRemoveFromHash: RegExp = /[!"#$%&'()*+,./:;<=>?@\^`{|}~]/g

function headingTextToHash(heading: string) {
    return '#' + heading.toLowerCase().replace(/ /g, '-')
        .replace(toRemoveFromHash, '');
}

function produceHeadingHash(headingNode: any): string {
    // @ts-ignore
    const joined = headingNode.children.map(n => n.value).join('');
    return headingTextToHash(joined);
}

class HeadingHierarchyParser {
    children: Array<any>;
    currIndex: number;
    headingDiscriminatorNumbers: Map<string, number>;
    
    constructor(c: Array<any>) {
        this.children = c;
        this.currIndex = 0;
        this.headingDiscriminatorNumbers = new Map();
    }
    
    isEnded(): boolean {
        return this.currIndex >= this.children.length;
    }
    
    parseNonHeadingContents(): Array<any> {
        const headingIndex = _.findIndexFrom(isHeading, this.currIndex, this.children);
        if (headingIndex === -1) {
            const result = this.children.slice(this.currIndex);
            this.currIndex = this.children.length;
            return result;
        }
        const result = this.children.slice(this.currIndex, headingIndex);
        this.currIndex = headingIndex;
        return result;
    }
    
    parseHierarchy(minDepth: number): Array<HeadingWithContent | any> {
        const base: Array<HeadingWithContent | any> = this.parseNonHeadingContents();
        
        while (!this.isEnded()) {
            const currHeading = this.children[this.currIndex];
            const currDepth = currHeading.depth;
            if (currDepth < minDepth) {
                return base;
            }
            this.currIndex += 1;//swallow the header
            
            const remaining = this.parseHierarchy(currDepth + 1);
            
            const originalHeadingHash = produceHeadingHash(currHeading);
            const number = this.headingDiscriminatorNumbers.get(originalHeadingHash);
            let discriminatedHeadingHash;
            if (number !== undefined) {
                this.headingDiscriminatorNumbers.set(originalHeadingHash, number + 1);
                discriminatedHeadingHash = originalHeadingHash + '-' + number;
            } else {
                this.headingDiscriminatorNumbers.set(originalHeadingHash, 1);
                discriminatedHeadingHash = originalHeadingHash;
            }
            currHeading.myHeadingHash = discriminatedHeadingHash;
            
            base.push({myType: 'heading_with_content', headingElement: currHeading, contents: remaining});
        }
        
        return base;
    }
    
    parseDocument(): HeadingWithContent {
        const contents = this.parseHierarchy(0);
        
        return {
            myType: 'heading_with_content',
            headingElement: null,//only the root has null heading
            contents: contents
        };
    }
}

function extractHeadingContentGroup(children: Array<any>): Array<HeadingWithContent | any> {
    
    var result = new HeadingHierarchyParser(children).parseHierarchy(0);
    
    
    return result;
}

function getRendererProps(props: ReactMarkdown.ReactMarkdownProps, tree: any): any {
    var renderers = xtend(defaultRenderers, props.renderers);
    
    var renderProps = xtend(props, {
        renderers: renderers,
        definitions: getDefinitions(tree)
    });
    return renderProps;
}

async function myParseMarkdownAsync(props: ReactMarkdownProps) {
    var src = props.source || props.children || '';
    
    var processor = unified().use(parse).use(props.plugins || []); // eslint-disable-next-line no-sync
    
    var tree = await processor.run(processor.parse(src));
    
    tree = wrapTableRows(tree);
    
    tree = addListMetadata()(tree);
    
    tree = naiveHtml(tree);
    
    // console.log(tree)
    
    return tree;
}

function WikiSection(props: { index: number, data: Array<any>, renderOptions: any }) {
    // const scrollNotifier = useContext(ScrollNotifierContext);
    const myRef = useRef(null);
    
    return <div ref={myRef}>{
        props.data.map((item: any, index: number) => {
            if (isHeadingWithContent(item)) {
                const childIndex: number = item.headingElement.position.start.line;
                return <React.Fragment key={index}>
                    {astToReact(item.headingElement, props.renderOptions)}
                    <WikiSection index={childIndex} key={childIndex} data={item.contents}
                                 renderOptions={props.renderOptions}/>
                </React.Fragment>
            } else {
                return astToReact(item, props.renderOptions);
            }
        })
    }</div>;
}

function RightBarSection(props: {
    outerSelectedHash: string, item: HeadingWithContent, layer: number,
    setOuterSelected: null | ((cond: boolean) => void),
    onTitleClick: () => void
}) {
    let [opened, setOpened] = useState(false);
    let [selected, setSelected] = useState(false);
    
    const history = useHistory()
    
    const item = props.item;
    
    const headingHash = item.headingElement.myHeadingHash;
    
    // console.log(headingHash);
    
    const hasContent = item.contents.findIndex(w => isHeadingWithContent(w)) !== -1;
    
    const domRef = useRef(null);
    
    useEffect(() => {
        var cond = props.outerSelectedHash === headingHash;
        
        setSelected(cond);
        
        if (props.setOuterSelected) {
            props.setOuterSelected(cond);
        }
        
        // if (cond) {
        //     if (domRef.current) {
        //         // @ts-ignore
        //         domRef.current.scrollIntoView({behavior: 'smooth'});
        //     }
        // }
        
    }, [headingHash, props.outerSelectedHash]);
    
    // @ts-ignore
    let toDisplay: string = item.headingElement.children.map(s => s.value).join(' ');
    
    // cut if too long
    if (toDisplay.length > 60) {
        toDisplay = toDisplay.substr(0, 60) + '...';
    }
    
    var headingColor = getHeadingColor(item.headingElement.depth);
    
    return <React.Fragment key={headingHash}>
        <ListItem ref={domRef} button style={{
            padding: 3, paddingLeft: props.layer * 10 + 10,
            display: "flex", flexDirection: "row", alignItems: "stretch"
        }} selected={
            selected
        } onClick={() => {
            if (!(opened && hasContent)) {
                history.push(headingHash);
                
                props.onTitleClick();
            }
            
            setOpened(!opened);
        }}>
            <div style={{
                display: "inline-flex", width: 5, background: headingColor,
                marginTop: 1, marginBottom: 0, marginRight: 5,
                flexShrink: 0
            }}/>
            {toDisplay}
            {hasContent && (opened ? <ExpandMore/> : <ExpandLess/>)}
        </ListItem>
        {hasContent && <Collapse in={opened} timeout={200}>
            <List dense={true} style={{padding:0}}>
                {item.contents.map((w, index) => {
                    if (!isHeadingWithContent(w)) {
                        return undefined;
                    }
                    return <RightBarSection
                        key={index}
                        outerSelectedHash={props.outerSelectedHash}
                        item={w}
                        layer={props.layer + 1}
                        setOuterSelected={(cond) => {
                            setSelected(cond);
                            
                            if (cond) {
                                setOpened(true);
                            }
                            
                            if (props.setOuterSelected) {
                                props.setOuterSelected(cond);
                            }
                        }}
                        onTitleClick={props.onTitleClick}
                    />;
                })}
            </List>
        </Collapse>}
    </React.Fragment>;
}

const ScrollNotifierContext = React.createContext({
    setSelectIndex: (selectIndex: number) => {
    },
    currentScrollY: 0
});

interface HeadingRef {
    headingHash: string,// has '#'
    element: HTMLElement
}

function getHeadingRefs(result: Array<HeadingRef>, hierarchy: Array<any>) {
    // console.log(hierarchy);
    hierarchy.forEach((item) => {
        if (isHeadingWithContent(item)) {
            const headingHash = item.headingElement.myHeadingHash;
            const element = document.getElementById(headingHash);
            if (element !== null) {
                result.push({
                    headingHash: headingHash,
                    element: element
                });
            } else {
                console.error('cannot find ' + headingHash)
            }
            
            getHeadingRefs(result, item.contents)
        }
    })
}

export function useWikiContent(
    contentUrl: string,
    renderers: any, plugins: any,
    scrollDomRef: MutableRefObject<DOMElement<any, any>>
): {
    wikiContentComponent: ReactElement,
    rightBarComponent: ReactElement,
    updateScrollFunc: () => void,
} {
    let reactMarkdownProps: ReactMarkdownProps = {
        renderers: renderers, plugins: plugins,
        children: 'loading markdown'
    };
    
    let [tree, setTree] = useState(null);
    
    const [errorLoading, setErrorLoading] = useState(false);
    
    useEffect(() => {
        async function doIt() {
            let markdownResult: AxiosResponse<any>;
            try {
                markdownResult = await Axios({url: contentUrl});
            } catch (e) {
                setErrorLoading(true);
                return
            }
            
            let parsedMarkdown = await myParseMarkdownAsync({
                // @ts-ignore
                renderer: renderers, plugins: plugins,
                children: markdownResult.data
            });
            
            // @ts-ignore
            setTree(parsedMarkdown);
        }
        
        doIt()
    }, []);
    
    const rendererProps: any = tree === null ? null : getRendererProps(reactMarkdownProps, tree);
    
    // @ts-ignore
    const hierarchy = tree === null ? null : extractHeadingContentGroup(tree.children);
    
    const [viewingSectionHash, setViewingSectionHash] = useState('');
    
    const wikiContents = hierarchy === null ? undefined :
        <WikiSection index={0} key={0} data={hierarchy} renderOptions={rendererProps}/>;
    
    const enableSectionUpdate = useRef(true);
    const timerRef = useRef(null);
    
    
    useEffect(() => () => {
        // @ts-ignore
        clearTimeout(timerRef.current);
    }, []);
    
    const headingRefs = useRef([]);
    
    useEffect(() => {
        if (hierarchy !== null) {
            const result: HeadingRef[] = [];
            getHeadingRefs(result, hierarchy);
            // @ts-ignore
            headingRefs.current = result;
            
            // console.log(result);
        }
    }, [hierarchy]);
    
    
    const doUpdateScroll = () => {
        if (getIsVerticalMobile()) {
            setViewingSectionHash('');
            return;
        }
        
        if (!scrollDomRef.current) {
            return;
        }
        
        if (!enableSectionUpdate.current) {
            return;
        }
        
        let active: HeadingRef | null = null;
        
        // @ts-ignore
        const scrollTop: number = scrollDomRef.current.scrollTop;
        // @ts-ignore
        const scrollHeight: number = scrollDomRef.current.scrollHeight;
        
        var screenHeight = document.documentElement.clientHeight;
        
        const scrollPosition = ((scrollTop / (scrollHeight - screenHeight) * scrollHeight))
        
        // const scrollPosition = scrollTop;
        
        // console.log(scrollPosition);
        
        for (let i = headingRefs.current.length - 1; i >= 0; i -= 1) {
            
            const item: HeadingRef = headingRefs.current[i];
            
            if (
                item.element &&
                item.element.offsetTop <
                scrollPosition + screenHeight / 8
            ) {
                active = item;
                break;
            }
        }
        
        if (active !== null && viewingSectionHash !== active.headingHash) {
            setViewingSectionHash(active.headingHash);
        }
    };
    
    const updateScrollMemoed = useCallback(doUpdateScroll, [viewingSectionHash]);
    
    const titles = hierarchy === null ? <></> : <List dense={true} style={{
        fontSize: 16
    }}>
        {hierarchy.map((v, index) => {
            if (!isHeadingWithContent(v)) {
                return undefined;
            }
            
            return <RightBarSection
                outerSelectedHash={viewingSectionHash}
                item={v}
                layer={0}
                key={index}
                setOuterSelected={null}
                onTitleClick={() => {
                    enableSectionUpdate.current = false;
                    // @ts-ignore
                    timerRef.current = setTimeout(() => {
                        enableSectionUpdate.current = true;
                        updateScrollMemoed();
                    }, 2000);
                }}
            />;
        })}
    </List>;
    
    // @ts-ignore
    const duration: number = tree ? tree.children.length * 70 : 1000;
    
    return {
        wikiContentComponent: <div>
            <TestAnimated shown={tree !== null} duration={duration}>
                {wikiContents}
            </TestAnimated>
            {errorLoading && <Typography>
                {'error loading ' + contentUrl}
            </Typography>}
            {(tree === null && !errorLoading) &&
            <CircularProgress/>
            }
        </div>,
        rightBarComponent: titles,
        updateScrollFunc: updateScrollMemoed
    };
}

